:root {
    --bg: #f5f7fb;
    --card: #ffffff;
    --primary: #0066cc;
    --accent: #00aaff;
    --muted: #6b7280;
    --glass: rgba(255, 255, 255, 0.6);
    --radius: 12px;
    --shadow-sm: 0 6px 18px rgba(15, 23, 42, 0.06);
}

* {
    box-sizing: border-box
}

html,
body,
#root {
    height: 100%
}

body {
    margin: 0;
    font-family: Inter, ui-sans-serif, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
    background: linear-gradient(180deg, var(--bg), #eef3fb);
    color: #111827;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.app-header {
    background: rgba(255, 255, 255, 0.06);
    backdrop-filter: blur(6px);
    padding: 18px 28px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 16px;
    border-bottom: 1px solid rgba(15, 23, 42, 0.04);
}

.brand {
    display: flex;
    align-items: center;
    gap: 12px;
    text-decoration: none;
    color: var(--primary);
}

.brand img {
    width: 44px;
    height: 44px;
    border-radius: 8px;
    box-shadow: var(--shadow-sm)
}

.brand .title {
    font-weight: 700;
    font-size: 1.05rem
}

.nav-links {
    display: flex;
    gap: 10px;
    align-items: center;
    flex-wrap: wrap
}

.nav-link {
    color: var(--muted);
    text-decoration: none;
    padding: 8px 12px;
    border-radius: 8px;
    transition: all .18s
}

.nav-link:hover {
    color: var(--primary);
    background: rgba(0, 102, 204, 0.06)
}

.user-actions {
    display: flex;
    gap: 10px;
    align-items: center
}

.main {
    padding: 32px;
    max-width: 1200px;
    margin: 24px auto
}

.footer {
    padding: 18px 28px;
    text-align: center;
    color: var(--muted);
    font-size: 0.9rem
}

.card {
    background: var(--card);
    border-radius: var(--radius);
    box-shadow: var(--shadow-sm);
    padding: 18px
}

.btn {
    border: none;
    padding: 8px 12px;
    border-radius: 10px;
    cursor: pointer;
    font-weight: 600
}

.btn--primary {
    background: linear-gradient(90deg, var(--primary), var(--accent));
    color: white
}

.btn--danger {
    background: #e11d48;
    color: white
}

.btn--ghost {
    background: transparent;
    border: 1px solid rgba(15, 23, 42, 0.06)
}

/* Responsive */
@media (max-width:800px) {
    .nav-links {
        display: none
    }
}

.modern-input {
    width: 100%;
    padding: 12px;
    border-radius: 10px;
    border: 1px solid #e6edf6;
    background: transparent
}

.modern-form {
    max-width: 420px;
    margin: 0 auto
}